import React, { useContext } from "react";
import { GlobalContext } from "../context/GlobalState";

export default function Question({ currentQuestion }) {
  const { questions, char, questionNumber } = useContext(GlobalContext);

  return (
    <div style={divStyle}>
      <h3>"{currentQuestion.questionBody}"</h3>
    </div>
  );
}

const divStyle = {
  backgroundColor: "#16A085",
  color: "white",
  padding: "0.25em",
  margin: "2em 1em",
  fontSize: "25px",
  fontFamily: "Bradley Hand ITC",
};
