import React, { useContext, useState, useEffect } from "react";
import { GlobalContext } from "../context/GlobalState";
import Rachel from "../img/rachel.jpg";
import Ross from "../img/ross.jpg";
import Monica from "../img/monica.jpg";
import Chandler from "../img/chandler.jpg";
import Phoebe from "../img/phoebe.jpg";
import Joey from "../img/joey.jpg";
import Gunther from "../img/gunther.jpg";
import Janice from "../img/janice.jpg";

export default function Card({ char, currentQuestion }) {
  // Sleep function
  function sleep(milliseconds) {
    const date = Date.now();
    let currentDate = null;
    do {
      currentDate = Date.now();
    } while (currentDate - date < milliseconds);
  }

  // State

  // Variables
  const { questions, questionNumber, answerSubmit } = useContext(GlobalContext);
  const rightAnswerId = currentQuestion.answerId;
  let charImg = Rachel;

  // Assigning Images

  switch (char.id) {
    case 1:
      charImg = Rachel;
      break;
    case 2:
      charImg = Ross;
      break;
    case 3:
      charImg = Monica;
      break;
    case 4:
      charImg = Chandler;
      break;
    case 5:
      charImg = Phoebe;
      break;
    case 6:
      charImg = Joey;
      break;
    case 7:
      charImg = Gunther;
      break;
    case 8:
      charImg = Janice;
      break;
  }

  return (
    <div
      onClick={() => {
        if (char.id == rightAnswerId) {
          answerSubmit(currentQuestion.id, "RIGHT ANSWER");
        } else {
          answerSubmit(currentQuestion.id, "WRONG ANSWER");
        }
      }}
      style={divStyle}
    >
      <div style={cardImage}>
        <img src={charImg} />
      </div>
      <h3 style={cardBody}>{char.name}</h3>
      <button style={{ display: "none" }}>Next</button>
    </div>
  );
}

const divStyle = {
  marginBottom: "2em",
};
const cardImage = {
  width: "150px",
  margin: "0 auto",
};
const cardBody = {
  backgroundColor: "#fff",
  color: "#D65555",
  width: "150px",
  margin: "auto",
  padding: "1.5rem 0",
  fontSize: "1.55rem",
  fontFamily: "Bradley Hand ITC",
};
