import React, { useContext } from "react";
import { GlobalContext } from "../context/GlobalState";

export default function Score() {
  const { score, previous, questions } = useContext(GlobalContext);
  if (questions.length > 0) {
    if (previous == 1) {
      return (
        <div>
          <h1>Nice!</h1>
          <h1>Score: {score} Points</h1>
        </div>
      );
    }
    if (previous == 0) {
      return (
        <div>
          <h1>Wrong!</h1>
          <h1>Score: {score} Points</h1>
        </div>
      );
    }
    if (previous == -1) {
      return (
        <div>
          <h1>Score: {score} Points</h1>
        </div>
      );
    }
  }
  return (
    <div>
      <h1>Score: {score} Points</h1>
    </div>
  );
}
