import React, { useContext } from "react";
import Card from "./Card.js";
import { GlobalContext } from "../context/GlobalState";

/* Randomize array in-place using Durstenfeld shuffle algorithm */
function shuffle(array) {
  var currentIndex = array.length,
    temporaryValue,
    randomIndex;

  // While there remain elements to shuffle...
  while (0 !== currentIndex) {
    // Pick a remaining element...
    randomIndex = Math.floor(Math.random() * currentIndex);
    currentIndex -= 1;

    // And swap it with the current element.
    temporaryValue = array[currentIndex];
    array[currentIndex] = array[randomIndex];
    array[randomIndex] = temporaryValue;
  }
  return array;
}

export default function Suggestions({ currentQuestion }) {
  const { questions, questionNumber, char } = useContext(GlobalContext);
  const rightAnswer = parseInt(currentQuestion.answerId);
  var arr = [];
  arr.push(rightAnswer - 1);
  while (arr.length < 4) {
    var r = Math.floor(Math.random() * 8);
    if (arr.indexOf(r) === -1) arr.push(r);
  }
  arr = shuffle(arr);
  return (
    <div style={divStyle}>
      <div>
        <Card char={char[arr[0]]} currentQuestion={currentQuestion} />
        <Card char={char[arr[1]]} currentQuestion={currentQuestion} />
      </div>
      <div>
        <Card char={char[arr[2]]} currentQuestion={currentQuestion} />
        <Card char={char[arr[3]]} currentQuestion={currentQuestion} />
      </div>
    </div>
  );
}

const divStyle = {
  display: "flex",
  justifyContent: "space-around",
};
